import React from 'react'
import { Link, navigate } from 'gatsby'
import cx from 'classnames'

import styles from './StandartPage.module.scss'

import helpers from '../../../helpers'

import ArrDown from '../../../images/svgs/chevronDown.inline.svg'


function StandartPageLayout({ children, location }) {
    const pathname = location ? location.pathname : '/404/'
    const mainPath = location ? `/${location.pathname.split('/')[1]}/` : '/404/'
    const mobSelectedTab = location.state ? location.state.selectedPage : ''
    const { LinksMenu } = helpers.constants.links
    const myPage = LinksMenu.find(item => item.link === mainPath) || {
        header: 'Not found',
        icon: '',
        link: '/404/',
        navTabs: [],
    }

    return (
        <div className={styles.page}>

            <div className={styles.desktop}>

                <h1 className={styles.page__title}>
                    {myPage.icon}
                    {myPage.header}
                </h1>

                {
                    myPage.navTabs && (
                        <nav className={cx([styles.page__tabs, styles.tabs])}>
                            {myPage.navTabs.map(item => (
                                <Link
                                    to={item.link}
                                    className={cx([
                                        styles.tab,
                                        {
                                            [styles.tab_active]: pathname === item.link,
                                        },
                                    ])}
                                    key={item.text}
                                >
                                    <span>{item.text}</span>
                                </Link>
                            ))}
                        </nav>
                    )
                }

                {children}

            </div>

            {/* Mobile starts here */}

            <div className={styles.mobile}>

                <nav className={styles.mobTabs}>
                    {LinksMenu.map(item => (
                        <div className={styles.mobTab} key={item.title}>
                            <input type="radio" name="mobileTabs" id={item.link} defaultChecked={item.link === mobSelectedTab} />
                            <label
                                className={cx([
                                    styles.mobTab__title,
                                    {
                                        [styles.openedManually]: item.link === mobSelectedTab,
                                    },
                                ])}
                                htmlFor={item.link}
                                onClick={e => (pathname !== item.link
                                    ? setTimeout(() =>
                                        navigate(item.link, {
                                            state: {
                                                selectedPage: item.link,
                                            },
                                        }), 300)
                                    : e.target.classList.add(styles.openedManually)
                                )}
                            >
                                {item.title}
                                <ArrDown />
                            </label>
                            <div className={styles.mobTab__content}>
                                <div className={styles.mobTab__contentContainer}>
                                    {
                                        pathname === item.link && children
                                    }
                                </div>
                            </div>
                        </div>
                    ))}
                </nav>

            </div>
        </div>
    )
}

export default StandartPageLayout
