import React from 'react'
import Loadable from 'react-loadable'

import Loader from '../Loader'


const MyProfileComponent = Loadable({
    loader: () => import('./Main'),
    loading() {
        return <Loader />
    },
    delay: 0,
})

const MyProfile = () => (
    <div>
        <MyProfileComponent />
    </div>
)

export default MyProfile
